<template>
    <div style="position: relative;width: 100%;height: 100%">
    <div class="good_left">
        <div class="box">
        <!-- 左边导航栏开始 -->
        <div class="box_left">
            <div v-for='(v,i) in list' :class="{'active':i==num}" :key="i" @click='scoll(i)'>{{v.name}}</div>
        </div>
        <!-- 左边导航栏结束 -->

        <!-- 右边开始 -->
        <div ref="box_right" class="box_right" @scroll="listScroll()">
            <ul v-for='(v,i) in list' :key="i" ref="box">
                <li class="nav">
                    <h2>{{v.name}}</h2>
                    <div>{{v.description}}</div>
                </li>
                <!-- 单个开始 -->
                <li class="content" v-for='(item,key) in v.foods' :key="key">
                    <div class="image">
                        <img :src="'//elm.cangdu.org/img/'+item.image_path" />
                    </div>
                    <div class="imgright">
                        <h2>{{item.name}}</h2>
                        <div class="ir1">{{item.description}}</div>
                        <div class="ir2">月售{{item.month_sales}}份 好评率{{item.satisfy_rate}}%</div>
                        <!-- <div v-if='item.activity.image_text!=null'>{{item.activity.image_text}}</div> -->
                        <!-- <div v-else></div> -->
                        <div class="ir3">￥{{item.specfoods[0].price}}<span>起</span></div>
                    </div>
                    <div class="add">
                        <!-- <button @click='minus(key)'>-</button> -->
                        <button @click='ball($event,item,key)'>+</button>
                    </div>
                </li>
                <!-- 单个结束 -->
            </ul>
        </div>
        <!-- 右边结束 -->
        </div>
        
    </div>
    <!-- 底部开始 -->
    <div class="foot">
      <!-- 购物车图片 -->

      <div class="img">
          <van-button type="primary" id="van" @click="showPopup">
          <van-icon name="shopping-cart-o" class="shop"/>
          <div>{{totalNum}}</div>
          </van-button>
      </div>
      <ul>
          <li style="font-size: 0.4rem;font-weight: 600;">￥{{totalPrice}}</li>
          <li style="margin-left: 0.1rem">配送费￥5</li>
      </ul>

      <div class="butt" v-if='price>=20' @click="$router.push('/Moneyout')">
          去结算
      </div>
      <div class="butt" v-else style="background: #535356">
          还差￥20起送
      </div>
    </div>
    <!-- 底部结束 -->
    <!-- 弹出 -->
    <van-popup v-model="show" position="bottom" class="cart">
        <!-- 头部开始 -->
        <div class="top">
            <div style="float: left">购物车</div>
            <div style="float: right;font-size: 0.2rem;" @click="clear()">
                <van-icon name="delete" /> 清空
            </div>
        </div>
        <div class="cartData" v-for='(v,i) in this.$store.state.arr' :key="i">
            <h4>{{v.name}}</h4>
            <h5>￥{{v.price}}</h5>
            <div>
              <button style="border: 1px solid #3190E8;color:#3190E8;background: white" @click='minus(v,i)'>-</button>
               {{v.num}}
              <button style="background: #3190E8;color:white" @click='addNum(i)'>+</button>
            </div>
            
        </div>
        <!-- 头部结束 -->


    </van-popup>
     </div>
</template>

<script>
import jq from 'jquery'   
import axios from 'axios'    
export default{
    name:'Left',
    data() {
        return {
            list:[], // 详情页侧边导航
            num:0,
            HeightTop:[],
            price:'',
            show:false
        }
    },
    computed: {
        // 总价
        totalPrice(){
            let arr = this.$store.state.arr
            let price = 0
            for(let i in arr){
                price += arr[i].price*arr[i].num
            }
            this.price = price
            return price
        },

        // 数量
        totalNum(){
            let arr = this.$store.state.arr
            let num = 0
            for(let i in arr){
                num += arr[i].num
            }
            return num
        }
    },
    created(){
        // 详情页
        axios.get('https://elm.cangdu.org/shopping/v2/menu?restaurant_id=1').then((res)=>{
            console.log(res)
            this.list = res.data
            this.$nextTick(()=>{
                this.height()
            })
            // 添加事件 参数一：事件  参数二：方法名
            window.addEventListener('scroll',this.listScroll)
        })
        
    },
    methods: {

        // 数量加
        addNum(i){
            // console.log(i)
            this.$store.commit('addNum',i)
        },

        // 数量减
        minus(v,i){
            // console.log(v)
            if(v.num<=1){
                this.$store.commit('del',i)
            }else{
                this.$store.commit('minus',i)
            }
        },

        // 清空购物车
        clear(){
            this.$store.commit('clear')
            this.show = false
        },


        scoll(index){
            // this.num = i
            // // console.log(jq('.box_right').animate)
            // jq('.box_right').animate({scrollTop:this.HeightTop[i]},500,function(){
            //         window.removeEventListener('scroll',this.listScroll)
            // })
            var height=0
            for(var i=0;i<index;i++){
                height+=this.$refs.box[i].clientHeight
            }
             this.num=index
              window.removeEventListener('scroll',this.listScroll)
            jq('.box_right').animate({scrollTop:height},500,function() {
                window.addEventListener('scroll',this.listScroll)
            })
        },

        listScroll(){
            // 右边最大的盒子
            let scrollY = this.$refs.box_right.scrollTop
            // 循环
            for(let i in this.HeightTop){
                let height1 = this.HeightTop[i]
                // let height2 = this.HeightTop[i+1] 
                if(scrollY>height1){
                    this.num=i
                }
            }
        },

        height(){
            // 获取元素
            var box = this.$refs.box;
            // console.log(box)
            // 定义一个高
            var height = 0
            // 把第一个高添加到数组
            this.HeightTop.push(height)
            // 循环
            for(let i=0;i<box.length;i++){
                // 累加
                height+=box[i].clientHeight
                // 添加到数组
                this.HeightTop.push(height)
            }
        },
        ball(event,item,key){

            // 加入购物车
            // console.log(item)
            let obj={id:item.specfoods[0].food_id,name:item.specfoods[0].name,price:item.specfoods[0].price,num:1,ku_id:item.specfoods[0].sku_id,packing_fee:item.specfoods[0].packing_fee,stock:item.specfoods[0].stock,img:item.image_path}
            let arr = this.$store.state.arr
            // let arr = this.$store.commit('addShop',obj)
            let index = arr.findIndex((v,i,arr)=>{
                return  v.id == item.specfoods[0]._id
            })

            if(index==-1){
                this.$store.commit('addShop',obj)
            }else{
                this.$store.commit('addNum',index)
            }

            // console.log(key)

            let div=document.createElement('div');
            div.className='ballFather';
            document.body.appendChild(div);

            let sonDiv=document.createElement('div');
            sonDiv.className='ballSon';
            div.appendChild(sonDiv);

            // console.log(event.target.getBoundingClientRect().top);
            let top=event.clientY/100;//获得 跳动小球top
            let left=event.clientX/100;//获得 left
            div.style.left=left+'rem';
            div.style.top=top+'rem';

            let y = (window.innerHeight/86 - top);// 下落的高度
            div.style.webkitTransform = `translateY(${y}rem)`;
            div.style.transform = `translateY(${y}rem)`;


            sonDiv.style.webkitTransform = `translateX(-${left-0.1}rem)`;
            sonDiv.style.transform = `translateX(-${left-0.1}rem)`;
            //保证 小球运动结束后，调用购物车的动画

            setTimeout(()=>{
            // document.querySelector('.sc').classList 找到class sc 的元素 添加一个动画
            document.querySelector('.img').classList.add("animation");
            setTimeout(()=>{
            document.querySelector('.img').classList.remove("animation");
            },400)
            },500)

        },

        // 弹出
        showPopup() {
            this.show = true;
        }

    },
}
</script>

<style lang="scss">
.good_left{
    width: 100%;
    position: relative;
    .box{
        width: 100%;
        position: relative;
        .box_left{
            width: 25%;
            height: 100%;
            position: fixed;
            top:0.8rem;
            overflow: hidden;
            overflow: scroll;
            div{
                flex:0 0 1rem;
                width:100%;
                height: 0.6rem;
                border-bottom: 1px solid gainsboro;
                font-size: 0.2rem;
                text-align: center;
                line-height: 0.6rem;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
                background: #F5F5F5;
            }
            .active{
                background: white;
            }
        }
        .box_right{
            width: 75%;
            height: 100%;
            position: fixed;
            overflow: scroll;
            /* overflow: hidden; */
            top: 0.8rem;
            left: 25%;
            ul{
                width:100%;
                .nav{
                    width: 100%;
                    height: 0.5rem;
                    background: #F5F5F5;
                    display: flex;
                    line-height: 0.6rem;
                    /* border: 1px solid red; */
                    padding-left: 0.1rem;
                    div{
                        margin-top:0.05rem;
                        margin-left:0.1rem;
                    }
                }
                .content{
                    width: 100%;
                    height: 1.3rem;
                    border-bottom: 1px solid #F5F5F5;
                    display: flex;
                    .image{
                        width: 20%;
                        height:100%;
                        padding:0.2rem 0.25rem;
                        img{
                            width: 0.7rem;
                            height: 0.7rem;
                        }
                    }
                    .imgright{
                        width: 60%;
                        height: 100%;
                        padding: 0.1rem 0rem;
                        h2{
                            width:100%;
                            font-weight: 600;
                            overflow: hidden;
                            text-overflow:ellipsis;
                            white-space: nowrap;
                        }
                        .ir1{
                            width: 100%;
                            color: gray;
                            font-size: 0.18rem;
                        }
                        .ir2{
                            width: 100%;
                            color: black;
                            font-size: 0.18rem;
                        }
                        .ir3{
                            width: 100%;
                            color: #FF6600;
                            font-size: 0.18rem;
                            font-weight: 600;
                            span{
                                color:gray;
                                font-size:0.16rem;
                                margin-left:0.1rem;
                                font-weight:100;
                            }
                        }
                    }
                    .add{
                        width: 20%;
                        height: 100%;
                        margin-top: 18%;
                        text-align: right;
                        margin-right: 0.2rem;
                        button{
                            width:0.25rem;
                            height:0.25rem;
                            border:none;
                            outline:none;
                            text-align: center;
                            line-height: 0.1rem;
                            color:white;
                            background:#3190E8;
                            border-radius: 50%;
                            
                        }
                    }
                }
            }
        }
    }
}
.foot{
    width: 100%;
    height: 1rem;
    background: #3D3D3F;
    position: fixed;
    left: 0;
    bottom: 0;
    color: white;
    display: flex;
    
    .img{
        width: 1rem;
        height:1rem;
        border-radius: 50%;
        background:#3190E8;
        position:relative;
        bottom:0.3rem;
        left:0.2rem;
        border:0.1rem solid #444444;
        /* text-align: center; */
        /* line-height: 1.1rem; */
        #van{
            width: 1rem;
            height:1rem;
            border:none;
            outline:none;
            background:none;
        .shop{
            font-size: 0.5rem;
            margin-left:-0.2rem;
        }
        div{
            width: 0.3rem;
            height: 0.3rem;
            position: absolute;
            bottom: 0.8rem;
            left: 0.6rem;
            background: red;
            /* color: white; */
            font-size: 0.2rem;
            z-index: 1111;
            font-weight: 600;
            line-height: 0.3rem;
            border-radius: 50%;
        }
        }
    }
    ul{
        width:50%;
        height: 1rem;
        color: white;
        font-size: 0.2rem;
        margin-left: 0.5rem;
    }

    .butt{
        width: 30%;
        background: #4CD964;
        font-size: 0.3rem;
        font-weight: 600;
        text-align: center;
        line-height: 1rem;
    }
    
}
.ballFather{
position: absolute;
width: 0.3rem;
height: 0.3rem;
border-radius: 50%;
transition:all 0.6s cubic-bezier(0.65, 0.06, 0.94, 0.31);
/* */
/* y轴很慢 x轴运动很快 1s y走 10 px x走 20px */
}
.ballSon{
width: 0.3rem;
height: 0.3rem;
background: #3190e8;
border-radius: 50%;
transition:all 0.6s linear;
}
@keyframes ani{
0{transform: scale(0)}
20%{transform: scale(1.2)}
40%{transform: scale(.8)}
80%{transform: scale(1.1)}
100%{transform: scale(1)}
}
.animation{
animation: ani .4s linear;
}
.cart{
    width: 100%;
    .top{
        width: 100%;
        height:0.7rem;
        background:#ECEFF1;
        font-size:0.3rem;
        line-height:0.7rem;
        padding:0 0.2rem;
    }
    .cartData{
        width: 100%;
        height: 0.8rem;
        font-size: 0.28rem;
        display: flex;
        padding: 0 0.2rem;
        h4,h5{
            font-weight:600;
            height: 0.8rem;
            line-height: 0.8rem;
        }
        h4{
            width: 60%;
            
        }
        h5{
            width: 20%;
        }
        &>div{
            width: 20%;
            height: 0.8rem;
            line-height: 0.8rem;
            button{
                width:0.3rem;
                height:0.3rem;
                text-align: center;
                line-height:0.1rem;
                border-radius: 50%;
                border:none;
                outline:none;
                margin-left:0.1rem;

            }
        }
    }


}

</style>


